.faq {
    @include pad {
        @include pad-p-y-2;
      }
      @include mobile {
        @include mobile-p-y-1;
    }
    margin-bottom: 36px;
    font-size: $text-lm;    
    font-weight: bold;
    @include pad {
        margin-bottom: 32px;
    }
    @include mobile {
        margin-bottom: 24px;
        h3 {
            font-size: $text-ls;
        }
    }    
}

.faq-list {
    li {
        &:not(:last-child) {
            margin-bottom: 32px;
            @include pad {
            margin-bottom: 24px;
            }
        }
    }
    h4 {
        margin-bottom: 16px;
        font-size: $text-ls;          
        font-weight: bold;          
        @include mobile {                
            margin-bottom: 8px;
            font-size: $text-base;
        }
    }
    p {
        margin-left: 95px;
        font-size: $text-base;                    
        @include mobile {
            margin-left: 0;
            font-size: $text-ms;
        }
    }    
}

.tab {
    @include mobile {
      margin-bottom: 21px;
    }
}